<template>
  <div>
    <el-button type="primary" @click="visible=true" v-if="isUnionOwner"></el-button>
    <!--   -->
    <div class="NewActivityCard">
      <el-dialog title="" :visible.sync="visible" @close="resetData">
        <hr>
        <el-form ref="form" :model="form" :rules="rules" label-width="100px">
          <el-form-item label="" prop="name">
            <el-input v-model="form.name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="price">
            <el-input v-model="form.price" placeholder="">
              <template slot="prepend"></template>
            </el-input>
          </el-form-item>
          <el-form-item label="" prop="color">
            <!-- todo  -->
            <union-color-picker @colorSelect="colorSelect"></union-color-picker>
          </el-form-item>
          <el-form-item label="" prop="amount">
            <el-input v-model="form.amount" placeholder="" @keyup.native="check()"></el-input>
            <span></span>
          </el-form-item>
          <el-form-item label="" prop="validityDay">
            <el-input v-model="form.validityDay" placeholder=""></el-input>
            <span></span>
          </el-form-item>
          <el-form-item label="" prop="applyTime">
            <el-date-picker v-model="form.applyTime" type="datetimerange" placeholder="">
            </el-date-picker>
            <span></span>
          </el-form-item>
          <el-form-item label="" prop="sellTime">
            <el-date-picker v-model="form.sellTime" type="datetimerange" placeholder="">
            </el-date-picker>
            <span></span>
          </el-form-item>
          <el-form-item label="" prop="illustration">
            <el-input type="textarea" v-model="form.illustration"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-switch v-model="form.isProjectCheck" on-text="" off-text="">
            </el-switch>
            <span></span>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="activityCardConfirm('form')"></el-button>
          <el-button @click="visible=false"></el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import $http from '@/utils/http.js';
import UnionColorPicker from '@/components/public-components/UnionColorPicker';
import { numberCheck } from '@/utils/filter.js';
import { activityCardIllustrationPass } from '@/utils/validator.js';
export default {
  name: 'add-activity-card',
  components: {
    UnionColorPicker
  },
  data() {
    // 
    let applyTimePass = (rule, value, callback) => {
      if (!value[0]) {
        return callback(new Error(''));
      } else if (value[0].getTime() < new Date()) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    let sellTimePass = (rule, value, callback) => {
      if (!value[0]) {
        return callback(new Error(''));
      } else if (value[0].getTime() < this.form.applyTime[1].getTime()) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    return {
      visible: false,
      form: {
        name: '',
        price: '',
        color: '03ABFF,008CD4',
        amount: '',
        validityDay: '',
        applyTime: ['', ''],
        sellTime: ['', ''],
        illustration: '',
        isProjectCheck: ''
      },
      rules: {
        name: [{ required: true, message: '', trigger: 'blur' }],
        price: [{ required: true, message: '', trigger: 'blur' }],
        color: [{ required: true, message: '', trigger: 'change' }],
        amount: [{ required: true, message: '', trigger: 'blur' }],
        validityDay: [{ required: true, message: '', trigger: 'blur' }],
        applyTime: [{ validator: applyTimePass, trigger: 'change' }],
        sellTime: [{ validator: sellTimePass, trigger: 'change' }],
        illustration: [{ validator: activityCardIllustrationPass, trigger: 'blur' }]
      }
    };
  },
  computed: {
    isUnionOwner() {
      return this.$store.state.isUnionOwner;
    },
    unionId() {
      return this.$store.state.unionId;
    }
  },
  mounted: function() {},
  methods: {
    // 
    colorSelect(v) {
      this.form.color = v;
    },
    // 
    check() {
      this.form.amount = numberCheck(this.form.amount);
    },
    // 
    activityCardConfirm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let url = `/unionCardActivity/unionId/${this.unionId}`;
          let data = {};
          data.name = this.form.name;
          data.price = this.form.price - 0;
          data.color = this.form.color;
          data.amount = this.form.amount - 0;
          data.validityDay = this.form.validityDay - 0;
          data.applyBeginTime = this.form.applyTime[0].getTime();
          data.applyEndTime = this.form.applyTime[1].getTime();
          data.sellBeginTime = this.form.sellTime[0].getTime();
          data.sellEndTime = this.form.sellTime[1].getTime();
          data.illustration = this.form.illustration;
          data.isProjectCheck = this.form.isProjectCheck - 0;
          $http
            .post(url, data)
            .then(res => {
              if (res.data.success) {
                eventBus.$emit('newActivityCard');
                this.$message({ showClose: true, message: '', type: 'success', duration: 5000 });
                this.visible = false;
              }
            })
            .catch(err => {
              this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
            });
        } else {
          return false;
        }
      });
    },
    // 
    resetData() {
      this.form = {
        name: '',
        price: '',
        color: '',
        amount: '',
        validityDay: '',
        applyTime: ['', ''],
        sellTime: ['', ''],
        illustration: '',
        isProjectCheck: ''
      };
      this.$store.commit('myColorChange', this.form.color);
    }
  }
};
</script>



