// 
export function numberCheck(value) {
  value = value + '';
  //.
  value = value.replace(/[^\d\.]/g, '');
  //.
  value = value.replace(/^\./g, '');
  //..
  value = value.replace(/\.{2,}/g, '.');
  //.
  value = value
    .replace('.', '$#$')
    .replace(/\./g, '')
    .replace('$#$', '.');
  return value;
}

//  yyyy-mm-dd hh:mm:ss
export function timeFilter(value) {
  value = new Date(value);
  let num = value + '';
  let date = '';
  let month = new Array();
  month['Jan'] = '01';
  month['Feb'] = '02';
  month['Mar'] = '03';
  month['Apr'] = '04';
  month['May'] = '05';
  month['Jun'] = '06';
  month['Jul'] = '07';
  month['Aug'] = '08';
  month['Sep'] = '09';
  month['Oct'] = '10';
  month['Nov'] = '11';
  month['Dec'] = '12';
  let week = new Array();
  week['Mon'] = '';
  week['Tue'] = '';
  week['Wed'] = '';
  week['Thu'] = '';
  week['Fri'] = '';
  week['Sat'] = '';
  week['Sun'] = '';
  let str = num.split(' ');
  date = str[3] + '-';
  date = date + month[str[1]] + '-' + str[2] + ' ' + str[4];
  return date;
}

