export function lengthCheck(inputValue, inputLength) {
  let valueLength = 0;
  let maxLenth = 0;
  let chinese = '[\u4e00-\u9fa5]'; // 21
  let str = inputValue || '';
  for (let i = 0; i < str.length; i++) {
    // 
    let temp = str.substring(i, i + 1); // 
    if (temp.match(chinese)) {
      // 1
      valueLength += 1;
    } else {
      // 0.5
      valueLength += 0.5;
    }
    if (Math.ceil(valueLength) == inputLength) {
      maxLenth = i;
    }
  }
  valueLength = Math.ceil(valueLength); //
  return [valueLength, maxLenth];
}

export function getStrLength(value) {
  let valueLength = 0;
  let chinese = '[\u4e00-\u9fa5]'; // 21
  let str = value + '' || '';
  for (let i = 0; i < str.length; i++) {
    // 
    let temp = str.substring(i, i + 1); // 
    if (temp.match(chinese)) {
      // 1
      valueLength += 1;
    } else {
      // 0.5
      valueLength += 0.5;
    }
  }
  return valueLength;
}



